import React, { useContext } from 'react';
import clsx from 'clsx';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
    Row,
    Col,
    Card,
    CardHeader,
    Input,
    Badge,
    ListGroup,
    ListGroupItem,
    Button,
    Progress
} from 'reactstrap';

import { SWSiteFilterContext } from 'contexts/SWSiteFilterContext';


import AUFlag from '../../assets/images/flags/AU.png';
import UKFlag from '../../assets/images/flags/GB.png';
import USFlag from '../../assets/images/flags/US.png';
import INFlag from '../../assets/images/flags/IN.png';

const countryMapping = {
    IN: INFlag,
    AU: AUFlag,
    US: USFlag,
    GB: UKFlag
}

const colorMapping = {
    WARNING: "warning",
    OVERSUBSCRIBE: "danger",
    UNDERSUBSCRIBE: "success"
}



export default function StatesInsightSiteList() {

    const { sites, setFilterState, filterState } = useContext(SWSiteFilterContext);
    const listGroupItems = filterState.map(site => {
        return (
            <ListGroupItem
                tag="a"
                action
                href="#/"
                onClick={(e) => e.preventDefault()}
                className="d-flex bg-white hover-scale-rounded justify-content-between align-items-center py-3">
                <div className="d-flex align-items-center">
                    <div className="avatar-icon-wrapper d-50 mr-3">
                        <div className="avatar-icon rounded-circle d-50">
                            <img alt="..." src={countryMapping[site.country]} />
                        </div>
                    </div>
                    <div>
                        <div className="font-weight-bold font-size-sm text-black">
                            {site.siteName}
                        </div>
                        <div className="d-flex align-items-center font-size-xs">
                            <Badge
                                color={colorMapping[site.status]}
                                className="badge-circle border-white border-1 mr-2">
                                Completed
                            </Badge>
                            <div className={`text-${colorMapping[site.status]}`}>{site.status}</div>
                        </div>
                    </div>
                </div>
                <div>
                    <Button size="sm" color="neutral-dark" className="px-3">
                        <span className="btn-wrapper--icon">
                            <FontAwesomeIcon icon={['far', 'eye']} />
                        </span>
                        <span className="btn-wrapper--label">Details</span>
                    </Button>
                </div>
            </ListGroupItem>
        )
    })

    return (
        <>
            <ListGroup flush>
                {listGroupItems}
            </ListGroup>
        </>
    )
}
