export default function ServiceWellnessSiteMapFilters() {
    const { sites, setFilterState, filterState } = useContext(SWSiteFilterContext);

    const [multiSelectState, setMultiSelectState] = useState({
        multi: true,
        disabled: false,
        loading: false,
        selectValues: [],
        searchBy: 'siteName',
        searchable: true,
        addPlaceholder: '+ click to add',
        labelField: 'siteName',
        valueField: 'id'
    })

    const selectorOnChange = (values)=>{
        setMultiSelectState({ ...multiSelectState, values });
        setFilterState([...values]);
        console.log(filterState);
    }

    return (
        <>
            <div className="p-4">
                <div className="heading-3 text-left text-primary">Sites Selection</div>
                <Select
                    options={sites}
                    placeholder="Select Sites..."
                    addPlaceholder={multiSelectState.addPlaceholder}
                    searchable={multiSelectState.searchable}
                    multi={multiSelectState.multi}
                    disabled={multiSelectState.disabled}
                    labelField={multiSelectState.labelField}
                    valueField={multiSelectState.valueField}
                    values={filterState}
                    onChange={selectorOnChange}
                />
            </div>
        </>
    )
}
