import React, { useState, useContext, useEffect, useRef } from 'react';
import 'leaflet/dist/leaflet.css';
import './style.css';
import {
    Row,
    Col,
    Card,
    CardHeader,
    CardFooter,
    CardBody,
    Badge,
    Table,
    TableContent,
    Button,
    UncontrolledPopover,
    ListGroup,
    ListGroupItem,
    Progress
} from 'reactstrap';

import {
    Map,
    TileLayer,
    Marker,
    Popup,
    LayerGroup,
    Circle,
    FeatureGroup,
    Rectangle,
} from 'react-leaflet';

import MarkerClusterGroup from 'react-leaflet-markercluster';
import Switch from 'rc-switch';
import { Filter } from 'react-feather';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import ServiceWellnessSiteMapFilters from '../ServiceWellnessSiteMapFilters';
import PerfectScrollbar from 'react-perfect-scrollbar';
import L from 'leaflet';
import redFilledMarker from '../../assets/images/mapIcons/redMarker.svg';
import orangeFilledMarker from '../../assets/images/mapIcons/orangeMarker.svg';
import { SWSiteFilterContext } from 'contexts/SWSiteFilterContext';
import StatesInsightSiteList from '../StatesInsightSiteList';
import StateInsightsStateAnalysis from '../StateInsightsStateAnalysis';

import AUFlag from '../../assets/images/flags/AU.png';
import UKFlag from '../../assets/images/flags/GB.png';
import USFlag from '../../assets/images/flags/US.png';
import INFlag from '../../assets/images/flags/IN.png';

const countryMapping = {
    IN: INFlag,
    AU: AUFlag,
    US: USFlag,
    GB: UKFlag
}

export default function StateInsightMap() {
    //toggle switch state
    const [checked, setChecked] = useState(false);

    const toggle = () => {
        setChecked(!checked);
    }


    // map coordinates
    const center = [-26.4390917, 133.281323];

    const { selectedSite, setSelectedSite, filterState } = useContext(SWSiteFilterContext);

    delete L.Icon.Default.prototype._getIconUrl;

    L.Icon.Default.mergeOptions({
        iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
        iconUrl: require('leaflet/dist/images/marker-icon.png'),
        shadowUrl: require('leaflet/dist/images/marker-shadow.png')
    });

    const createClusterCustomIcon = cluster => {
        return L.divIcon({
            html: `<span>${cluster.getChildCount()}</span>`,
            className: 'marker-cluster-custom',
            iconSize: L.point(40, 40, true),
        })
    }
    const redMarker = L.icon({
        iconUrl: redFilledMarker,
        iconSize: [40, 40],
        iconAnchor: [20, 40],
    });

    const orangeMarker = L.icon({
        iconUrl: orangeFilledMarker,
        iconSize: [40, 40],
        iconAnchor: [20, 40],
    });

    const colorMapping = {
        Neutual: "warning",
        'Strong Negative': "danger",
        'Strong Positive': "success",
        Positive: "success"
    }

    const siteMarkers = filterState.map(site => {
        let siteIcon;

        if (site.status === 'offline') {
            siteIcon = redMarker;
        }
        else if (site.status === 'degraded') {
            siteIcon = orangeMarker;
        }

        const popupDom = <Popup>
            <ListGroupItem className="rounded-top">
                <div className="align-box-row">
                    <div>
                        <div className="avatar-icon-wrapper avatar-icon-md">
                            <div className="avatar-icon rounded-circle">
                                <img alt="flag" src={countryMapping[site.country]} />
                            </div>
                        </div>
                    </div>
                    <div className="pl-2">
                        <span className="pb-1 d-block">
                            <span className="font-weight-bold">{site.siteName}</span>
                        </span>
                        <Badge color={colorMapping[site.status]}>{site.status}</Badge>
                    </div>
                </div>
            </ListGroupItem>
            <ListGroupItem className="bg-transparent p-0">
            <ListGroup flush>
              <ListGroupItem className="py-2">
                <div className="align-box-row py-1">
                  <div>
                    <div className="font-weight-bold">Positive</div>
                  </div>
                </div>
                <Progress
                  className="progress-animated-alt"
                  color="success"
                  value="34">
                  34%
                </Progress>
                <div className="align-box-row progress-bar--label mt-1 text-muted">
                  <small className="text-dark">0</small>
                  <div className="ml-auto">100%</div>
                </div>
              </ListGroupItem>
              <ListGroupItem className="py-2">
                <div className="align-box-row py-1">
                  <div>
                    <div className="font-weight-bold">Negative</div>
                  </div>
                </div>
                <Progress
                  className="progress-animated-alt"
                  color="danger"
                  value="100">
                  100%
                </Progress>
                <div className="align-box-row progress-bar--label mt-1 text-muted">
                  <small className="text-dark">0</small>
                  <div className="ml-auto">100%</div>
                </div>
              </ListGroupItem>
              <ListGroupItem className="py-2">
                <div className="align-box-row py-1">
                  <div>
                    <div className="font-weight-bold">Neutual</div>
                  </div>
                </div>
                <Progress
                  className="progress-animated-alt"
                  color="warning"
                  value="89">
                  89%
                </Progress>
                <div className="align-box-row progress-bar--label mt-1 text-muted">
                  <small className="text-dark">0</small>
                  <div className="ml-auto">100%</div>
                </div>
              </ListGroupItem>
              </ListGroup>
            </ListGroupItem>
            <ListGroupItem className="rounded-bottom p-2 text-center">
                <Button 
                    color={`neutral-${colorMapping[site.status]}`} 
                    size="sm" 
                    className="font-weight-bold btn-block"
                    onClick= {event=>{
                        setSelectedSite(site);
                    }}
                    >
                    <span>View Breakdown</span>
                </Button>
            </ListGroupItem>
        </Popup>


        return (siteIcon && site) ?
            <Marker
                position={site.coordinates}
                icon={siteIcon} >
                {popupDom}
            </Marker> :
            <Marker
                position={site.coordinates} >
                {popupDom}
            </Marker>
    })


    return (
        <>
            <Row>
                <Col xl="12">

                    <Card className="card-box mb-5">
                        <CardHeader>
                            <div className="card-header--title">
                                <h4 className="font-size-lg mb-0 py-2 font-weight-bold">
                                    Site View
                                    <span>
                                        <Badge pill color="dark" className="ml-2">
                                            Updated 1 hour ago
                                        </Badge>
                                    </span>
                                </h4>
                            </div>
                            <div className="card-header--actions d-flex justify-content-center">
                                {/* <div className="m-2 pt-2">
                                    <h4 className="font-size-sm py-1 text-black font-weight-bold">Table View</h4>
                                </div>

                                <div className="m-2 pt-2">
                                    <Switch
                                        checked={checked}
                                        onClick={toggle}
                                        className="switch-small"
                                    />
                                </div> */}

                                <div className="d-flex align-items-center popover-header-wrapper">
                                    <span className="d-inline-block pr-2">
                                        <Button
                                            id="siteFilters"
                                            color="neutral-primary"
                                            className="bg-neutral-primary text-primary font-size-lg p-0 d-inline-block shadow-none border-0 text-center d-44 rounded position-relative">
                                            {/* <Badge color="primary">
                                                Filters
                                            </Badge> */}
                                            <Filter />
                                        </Button>
                                        <UncontrolledPopover
                                            target="siteFilters"
                                            tigger="legacy"
                                            className="popover-custom-wrapper popover-custom-md"
                                            placement="auto">
                                            <ServiceWellnessSiteMapFilters />
                                        </UncontrolledPopover>

                                    </span>
                                </div>

                            </div>
                        </CardHeader>
                        <CardBody className="pb-0">
                            {/* MAP */}
                            <Row>
                                <Col xl="8" className="mb-2">
                                    <Map style={{ "height": "54vh" }} center={center} zoom={4.3} maxZoom={18}>
                                        {/* <TileLayer
                                            attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                                        /> */}
                                        <TileLayer
                                            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                                            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                        />
                                        <MarkerClusterGroup
                                            showCoverageOnHover={false}
                                            spiderfyDistanceMultiplier={2}
                                            iconCreateFunction={createClusterCustomIcon}>
                                            {siteMarkers}
                                        </MarkerClusterGroup>
                                    </Map>
                                </Col>
                                <Col xl="4" className="d-flex">
                                    <Card className="card-box mb-5 w-100 h-80">
                                        <CardHeader>
                                            <div className="card-header--title">
                                                <b>Site List</b>
                                            </div>
                                        </CardHeader>

                                        <div className="divider" />
                                        <div className="scroll-area scroll-area-lg shadow-overflow">
                                            <PerfectScrollbar
                                                options={{
                                                    wheelPropagation: false
                                                }}>
                                                <StatesInsightSiteList />
                                            </PerfectScrollbar>
                                        </div>
                                    </Card>
                                </Col>
                            </Row>

                            <tr className="divider"></tr>
                            <StateInsightsStateAnalysis />
                           
                        </CardBody>
                    </Card>

                </Col>
            </Row>

        </>
    )
}
